html,
body {
  position: relative;
  min-height: 100%;
}

html {
  box-sizing: border-box;
  font-size: 16px;
}

body {
  overflow-x: clip;
  font-size: 1rem;
}

.container {
  @each $breakpoint, $width in $container-max-widths {
    @include breakpoint(map.get($grid-breakpoint, $breakpoint)) {
      max-width: $width;
    }
  }

  width: 100%;

  margin: 0 auto;
  padding: 0 16px;
}

